import React from "react";
import { Button, Container, Row } from "reactstrap";
import sectiondata from "./../../../data/sections.json";

const Pricing = (props) => {
  let publicUrl = `${process.env.PUBLIC_URL}/`;
  let imagealt = "image";
  let data = sectiondata.pricing;

  return (
    <div className="user-list-area py-3">
      <Container>
        <div className=" text-center">
          <h2 className="sub-title">Choose the perfect package for you</h2>
        </div>
        <Row>
          {data.items.map((item, i) => (
            <div key={i} className="col-lg-3 col-sm-6">
              <div className="single-pricing text-center">
                <div className="thumb double-img">
                  <img src={publicUrl + item.icon} alt={imagealt} />
                </div>
                <div className="details">
                  <h4 className="title">{item.name}</h4>
                  <h3 className="price">{item.price}</h3>
                  <h6>Every Week</h6>
                  <ul>
                    {item.feature.map((featuresitem, i) => (
                      <li key={i}>{featuresitem}</li>
                    ))}
                  </ul>
                  <Button
                    className="btn btn-blue"
                    onClick={(i) => props.onPackageSelect(i)}
                  >
                    {item.btntxt}
                  </Button>
                </div>
              </div>
            </div>
          ))}
        </Row>
      </Container>
    </div>
  );
};

export default Pricing;
