import React from "react";
import sectiondata from "./../../../data/sections.json";
import { Row, Col, Container } from "reactstrap";

const Faq = () => {
  let data = sectiondata.faq;

  return (
    <div className="faq-area py-4">
      <Container>
        <Row>
          <Col lg={{ size: 6, offset: 3 }} className="mb-12">
            <div className=" text-center"></div>
            <div className="accordion" id="accordion">
              {/* single accordion */}
              {data.items.map((item, i) => (
                <div key={i} className="single-accordion card">
                  <div className="card-header" id={"headingOne" + i}>
                    <h2 className="mb-0">
                      <button
                        className="btn-link collapsed"
                        type="button"
                        data-toggle="collapse"
                        data-target={"#collapseOne" + i}
                        aria-expanded="false"
                        aria-controls={"collapseOne" + i}
                      >
                        {item.title}
                      </button>
                    </h2>
                  </div>
                  <div
                    id={"collapseOne" + i}
                    className={"collapse show" + item.class}
                    aria-labelledby={"headingOne" + i}
                    data-parent="#accordion"
                  >
                    <div className="card-body">{item.content}</div>
                  </div>
                </div>
              ))}
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default Faq;
